.tasklistheader {
  background-image: url(../images/girlstudy.jpg);
}

#task-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  grid-template-areas: "taskform tasklist";
}

#taskform {
  grid-area: taskform;
  max-width: 300px;
  margin: 0 auto;
}
#taskform label {
  display: block;
  width: 100%;
}
#taskform input {
  width: 100px;
}

#tasklist {
  grid-area: tasklist;
}

#page1 {
  padding-bottom: 5%;
}

#draganddrop {
